import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';

import {AuthService} from '@core/index';
import * as screenfull from 'screenfull';

import {Library} from '@core/index';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  class_header: any;
  isVn: boolean = true;
  _translate: any;
  datasAccount: any = null;

  constructor(
    private authService: AuthService,
    private router: Router,
    public translate: TranslateService,
  ) {
    if (this.authService.getLanguage() != 'vn') {
      this.isVn = false;
    }
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());

    this.class_header = 'app-header-system';
  }

  ngOnInit() {
    this.getInforAccount();
  }

  logout() {
    Library.confirmVA(this._translate.CONFIRM.logout, this._translate.NOTIFY.notify, () => {
      this.authService.logout();
      window.location.href = '/login';
    });
  }

  changLangugeVn() {
    this.isVn = true;
    this.authService.setLanguage('vn');
    this.translate.use('vn');

  }

  changLangugeEn() {
    this.isVn = false;
    this.authService.setLanguage('en');
    this.translate.use('en');

  }

  getInforAccount() {
    const dataAcount = JSON.parse(window.localStorage.getItem('currentUser'));
    this.datasAccount = dataAcount.account;
  }

  fullScreen() {
    if (screenfull.enabled) {
      screenfull.toggle();
    }
  }
}
