import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/filter';
import { AuthService } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
// import {  } from "";

@Component({
  selector: 'app-breadcrumbs',
  templateUrl: './breadcrumbs.component.html'
})
export class BreadcrumbsComponent implements OnInit, OnDestroy {
  breadcrumbs: Array<Object>;
  sub: Subscription;
  _translate: any;

  constructor(
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute,
    public translate: TranslateService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.sub = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event) => {
      this.breadcrumbs = [

      ];
      let currentRoute = this.route.root, url = '';
      // console.log(currentRoute);
      let i = 0;
      do {
        const childrenRoutes = currentRoute.children;
        currentRoute = null;
        // tslint:disable-next-line:no-shadowed-variable
        childrenRoutes.forEach(route => {
          if (route.outlet === 'primary') {
            const routeSnapshot = route.snapshot;
            if (routeSnapshot.url.length) {
              url += '/' + routeSnapshot.url.map(segment => segment.path).join('/');
              if (route.snapshot.data.custom_brec) {
                this.breadcrumbs = route.snapshot.data.custom_brec;
                return true;
              }
              let brec = route.snapshot.data.brec;
              let lable = this._translate.MENU[brec];
              if (lable)
                route.snapshot.data.brec = lable;
              this.breadcrumbs.push({
                label: route.snapshot.data,
                url: url
              });
            }
            currentRoute = route;
          }
        });
      } while (currentRoute);
    });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}
