import { NgModule, SkipSelf, Optional } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { ModalModule } from 'ngx-bootstrap/modal';
import { NotificationModule } from '@shared/index';
import { vTranslateModule } from '@core/module/v-translate.module';

import {AsideToggleDirective,NAV_DROPDOWN_DIRECTIVES,ReplaceDirective,SIDEBAR_TOGGLE_DIRECTIVES} from './directives';
const APP_DIRECTIVES = [AsideToggleDirective,NAV_DROPDOWN_DIRECTIVES,ReplaceDirective,SIDEBAR_TOGGLE_DIRECTIVES]

import { 
  HeaderComponent,
  APP_SIDEBAR_NAV,
  FooterComponent,
  BreadcrumbsComponent
} from './components';

@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    BsDropdownModule.forRoot(),
    ModalModule.forRoot(),
    NotificationModule,
    vTranslateModule
  ],
  declarations: [
    FooterComponent,
    HeaderComponent,
    BreadcrumbsComponent,
    APP_SIDEBAR_NAV,
    ...APP_DIRECTIVES
  ],
  exports: [
    FooterComponent,
    HeaderComponent,
    BreadcrumbsComponent,
    APP_SIDEBAR_NAV,
    ...APP_DIRECTIVES,
  ],
  providers: []
})
export class LayoutModule {
  constructor( @Optional() @SkipSelf() parentModule: LayoutModule) {
    if (parentModule) {
      throw new Error(
        'LayoutModule is already loaded. Import it in the AppModule only');
    }
  }
}
